<template>
    <div>
      <h2>User Dashboard</h2>
      <div v-if="booking">
        <p><strong>Show Name:</strong> {{ booking.showname }}</p>
        <p><strong>Venue Name:</strong> {{ booking.venuename }}</p>
        <p><strong>Show Time:</strong> {{ booking.showtime }}</p>
        <p><strong>Date:</strong> {{ booking.date }}</p>
        <p><strong>Number of Seats:</strong> {{ booking.number_of_seats }}</p>
      </div>
      <div v-else>
        <p>No booking information available.</p>
      </div>
    </div>
  </template>
  
  <script>
  import axios from 'axios';
  
  export default {
    data() {
      return {
        booking: null,
      };
    },
    created() {
      this.fetchBookingInfo();
    },
    methods: {
      fetchBookingInfo() {
        axios.get('/book_show')
          .then(response => {
            if (response.status === 200) {
              this.booking = response.data.booking;
            }
          })
          .catch(error => {
            console.error('Error fetching booking information:', error);
          });
      },
    },
  };
  </script>
  
  
  
  
  
  