<template>
    <div class="admin-dashboard">
      <!-- Venue Details Section -->
      <div class="jumbotron vertical-cent">
        <!-- ... Your Venue Details Template ... -->
        <div class="container">
            <link rel="stylesheet" href="https://cdn.jsdelivr.net/npm/bootswatch@4.5.2/dist/flatly/bootstrap.min.css" integrity="sha384-qF/QmIAj5ZaYFAeQcrQ6bfVMAh4zZlrGwTPY7T/M+iTTLJqJBJjwwnsE5Y0mV7QK" crossorigin="anonymous">
        <div class="row">
            <div class="col-sm-12">
                <br /><br />
                
                <h1>Venue Details</h1>
                <hr><br>
                <!--alert messg-->
                <b-alert variant="success" v-if="showMessage" show> {{ message }} </b-alert>
                <button type="button" class="btn btn-success btn-sm" v-b-modal.venue-modal>Add Venue</button>
            
                <br><br>
                <table class="table table-hover">
                    <thead>
                        <tr>
                            <!--table header cells-->
                            <th scope="col">Venue Name</th>
                            <th scope="col">Venue Place</th>
                            <th scope="col">Venue City</th>
                            <th scope="col">Venue Capacity</th>
                            <th scope="col">Shows</th>
                            <th scope="col">Actions</th>  
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="venue, index in venues" :key="index">
                            <td>{{ venue.name }}</td>
                            <td>{{ venue.place }}</td>
                            <td>{{ venue.city }}</td>
                            <td>{{ venue.capacity }}</td>
                            <td>

                                <ul>
                                    <li v-for="show in venue.shows" :key="show">{{ show }}</li>
                                </ul>

                            </td>
                            <td>
                                <div class="btn-group" role="group">
                                    <button type="button" class="btn btn-info btn-sm" v-b-modal.venue-update-modal 
                                    @click="editVenue(venue)">Update</button>
                                    <button type="button" class="btn btn-danger btn-sm"
                                    @click="deleteVenue(venue)">Delete</button>
                                </div>
                            </td>

                        </tr>
                    </tbody>
                </table>
    
            </div>
        </div>

        <!--first modal-->
        <b-modal ref="addVenueModal"
                 id="venue-modal"
                 title="Add a new venue" hide-backdrop hide-footer>
        <b-form @submit="onSubmit" @reset="onReset" class="w-100">
            <b-form-group id="form-name-group"
                          label="Name:"
                          label-for="form-name-input">
                <b-form-input id="form-name-input"
                              type="text"
                              v-model="addVenueForm.name"
                              placeholder="Enter venue">
                </b-form-input>
            </b-form-group>

            <b-form-group id="form-place-group"
                          label="Place:"
                          label-for="form-place-input">
                <b-form-input id="form-place-input"
                              type="text"
                              v-model="addVenueForm.place"
                              placeholder="Enter place">
                </b-form-input>
            </b-form-group>

            <b-form-group id="form-city-group"
                          label="City:"
                          label-for="form-city-input">
                <b-form-input id="form-city-input"
                              type="text"
                              v-model="addVenueForm.city"
                              placeholder="Enter city">
                </b-form-input>
            </b-form-group>

            <b-form-group id="form-capacity-group"
                          label="Capacity:"
                          label-for="form-capacity-input">
                <b-form-input id="form-capacity-input"
                              type="number"
                              v-model="addVenueForm.capacity"
                              placeholder="Enter capacity">
                </b-form-input>

            </b-form-group>
            
            <b-form-group id="form-shows-group" label="Shows:" label-for="form-shows-input">
                <div v-for="(show, index) in availableShows" :key="index">
                <b-form-checkbox
                    v-model="addVenueForm.shows"
                    :value="show"
                    >{{ show }}</b-form-checkbox>
                </div>
            </b-form-group>


            <b-button type="submit" variant="outline-info">Submit</b-button>
            <b-button type="reset" variant="outline-danger">Reset</b-button>

        </b-form>
        </b-modal>

<!-- start of modal 2 -->

<b-modal ref="editVenueModal"
                 id="venue-update-modal"
                 title="Update" hide-backdrop hide-footer>
        <b-form @submit="onSubmitUpdate" @reset="onResetUpdate" class="w-100">

            <b-form-group id="form-name-edit-group"
                          label="Name:"
                          label-for="form-name-edit-input">
                <b-form-input id="form-name-edit-input"
                              type="text"
                              v-model="editForm.name"
                              required
                              placeholder="Enter venue">
                </b-form-input>
            </b-form-group>

            <b-form-group id="form-place-edit-group"
                          label="Place:"
                          label-for="form-place-edit-input">
                <b-form-input id="form-place-edit-input"
                              type="text"
                              v-model="editForm.place"
                              required
                              placeholder="Enter place">
                </b-form-input>
            </b-form-group>

            <b-form-group id="form-city-edit-group"
                          label="City:"
                          label-for="form-city-edit-input">
                <b-form-input id="form-city-edit-input"
                              type="text"
                              v-model="editForm.city"
                              required
                              placeholder="Enter city">
                </b-form-input>
            </b-form-group>

            <b-form-group id="form-capacity-edit-group"
                          label="Capacity:"
                          label-for="form-capacity-edit-input">
                <b-form-input id="form-capacity-edit-input"
                              type="number"
                              v-model="editForm.capacity"
                              required
                              placeholder="Enter capacity">
                </b-form-input>

            </b-form-group>
            
            <b-form-group id="form-shows-edit-group" label="Shows:" label-for="form-shows-edit-input">
                <div v-for="(show, index) in availableShows" :key="index">
                <b-form-checkbox
                    v-model="editForm.shows"
                    :value="show"
                    >{{ show }}</b-form-checkbox>
                </div>
            </b-form-group>


            <b-button type="submit" variant="outline-info">Submit</b-button>
            <b-button type="cancel" variant="outline-danger">cancel</b-button>

        </b-form>
        </b-modal>

        
    </div>
      </div>
  
      <!-- Show Details Section -->
      <div class="jumbotron vertical-cent">
        <!-- ... Your Show Details Template ... -->
        <div class="container">
        <link rel="stylesheet" href="https://cdn.jsdelivr.net/npm/bootswatch@4.5.2/dist/darkly/bootstrap.min.css" integrity="sha384-nNK9n28pDUDDgIiIqZ/MiyO3F4/9vsMtReZK39klb/MtkZI3/LtjSjlmyVPS3KdN" crossorigin="anonymous">
        <div class="row">
            <div class="col-sm-12">
                <h1>Show Details</h1>
                <hr><br>
                <!--alert messg-->
                <b-alert variant="success" v-if="showMessage" show> {{ message }} </b-alert>
                <button type="button" class="btn btn-success btn-sm" v-b-modal.show-modal>Add Show</button>
                <br><br>
                <table class="table table-hover">
                    <thead>
                        <tr>
                            <!--table header cells-->
                            <th scope="col">Name</th>
                            <th scope="col">Genre</th>
                            <th scope="col">Rating</th>
                            <th scope="col">Price</th>
                            <th scope="col">Date</th>
                            <th scope="col">Time</th>  
                            <th scope="col">Available Seats</th>
                            <th scope="col">Venue</th>
                            <th scope="col">Actions</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="show, index in shows" :key="index">
                            <td>{{ show.show_name }}</td>
                            <td>{{ show.genre }}</td>
                            <td>{{ show.rating }}</td>
                            <td>{{ show.price }}</td>
                            <td>{{ show.date }}</td>   
                            <td>{{ show.time }}</td>
                            <td>{{ show.available_seats }}</td>
                            <td>{{ show.venue.name }} {{ show.venue.place }}</td>
                        
                            <td>
                                <div class="btn-group" role="group">
                                    <button type="button" class="btn btn-info btn-sm" v-b-modal.show-update-modal 
                                    @click="editShow(show)">Update</button>
                                    <button type="button" class="btn btn-danger btn-sm"
                                    @click="deleteShow(show)">Delete</button>
                                </div>
                            </td>

                        </tr>
                    </tbody>
                </table>
    
            </div>
        </div>

        <!--first modal-->
        <b-modal ref="addShowModal"
                 id="show-modal"
                 title="Add a new show" hide-backdrop hide-footer>
        <b-form @submit="onSubmit" @reset="onReset" class="w-100">
            <b-form-group id="form-name-group"
                          label="Name:"
                          label-for="form-name-input">
                <b-form-input id="form-name-input"
                              type="text"
                              v-model="addShowForm.name"
                              placeholder="Enter show">
                </b-form-input>
            </b-form-group>

            <b-form-group id="form-genre-group"
                          label="Genre:"
                          label-for="form-genre-input">
                <b-form-input id="form-genre-input"
                              type="text"
                              v-model="addShowForm.genre"
                              placeholder="Enter genre">
                </b-form-input>
            </b-form-group>

            <b-form-group id="form-rating-group"
                          label="Rating:"
                          label-for="form-rating-input">
                <b-form-input id="form-rating-input"
                              type="number"
                              v-model="addShowForm.rating"
                              placeholder="Enter rating">
                </b-form-input>
            </b-form-group>

            <b-form-group id="form-price-group"
                          label="Price:"
                          label-for="form-price-input">
                <b-form-input id="form-price-input"
                              type="number"
                              v-model="addShowForm.price"
                              placeholder="Enter price">
                </b-form-input>

            </b-form-group>
            <b-form-group id="form-date-group" label="Date:" label-for="form-date-input">
                <b-form-input id="form-date-input"
                            type="date"  
                            v-model="addShowForm.date"
                            required>
                </b-form-input>
            </b-form-group>

            <b-form-group id="form-time-group" label="Time:" label-for="form-time-input">
                <b-form-input id="form-time-input"
                            type="time" 
                            v-model="addShowForm.time"
                            required>
                </b-form-input>
            </b-form-group>

            <b-form-group id="form-available-seats-group"
                          label="Available Seats:"
                          label-for="form-available-seats-input">
                <b-form-input id="form-available-seats-input"
                              type="number"
                              v-model="addShowForm.available_seats"
                              placeholder="Enter available seats">
                </b-form-input>
            </b-form-group>

            <b-form-group id="form-venue-group" label="Venue:" label-for="form-venue-input">
                <b-form-select id="form-venue-input" v-model="addShowForm.venue" required placeholder="Select venue">
                    <option v-for="venue in venues" :key="venue.id" :value="venue.id">
                        {{ venue.name }} {{ venue.place }} {{ venue.city }}
                    </option>
                </b-form-select>
            </b-form-group>









            <b-button type="submit" variant="outline-info">Submit</b-button>
            <b-button type="reset" variant="outline-danger">Reset</b-button>

        </b-form>
        </b-modal>

<!-- start of modal 2 -->

<b-modal ref="editShowModal"
                 id="show-update-modal"
                 title="Update" hide-backdrop hide-footer>
        <b-form @submit="onSubmitUpdate" @reset="onResetUpdate" class="w-100">

            <b-form-group id="form-name-edit-group"
                          label="Name:"
                          label-for="form-name-edit-input">
                <b-form-input id="form-name-edit-input"
                              type="text"
                              v-model="editShowForm.name"
                              required
                              placeholder="Enter show">
                </b-form-input>
            </b-form-group>

            <b-form-group id="form-genre-edit-group"
                          label="Genre:"
                          label-for="form-genre-edit-input">
                <b-form-input id="form-genre-edit-input"
                              type="text"
                              v-model="editShowForm.genre"
                              required
                              placeholder="Enter genre">
                </b-form-input>
            </b-form-group>

            <b-form-group id="form-rating-edit-group"
                          label="Rating:"
                          label-for="form-rating-edit-input">
                <b-form-input id="form-rating-edit-input"
                              type="number"
                              v-model="editShowForm.rating"
                              required
                              placeholder="Enter rating">
                </b-form-input>
            </b-form-group>

            <b-form-group id="form-price-edit-group"
                          label="Price:"
                          label-for="form-price-edit-input">
                <b-form-input id="form-price-edit-input"
                              type="number"
                              v-model="editShowForm.price"
                              required
                              placeholder="Enter price">
                </b-form-input>

                <b-form-group id="form-available-seats-edit-group"
                          label="Available Seats:"
                          label-for="form-available-seats-edit-input">
                <b-form-input id="form-available-seats-edit-input"
                              type="number"
                              v-model="editShowForm.available_seats"
                              required
                              placeholder="Enter available seats">
                </b-form-input>
            </b-form-group>


            </b-form-group>
            <b-form-group id="form-date-edit-group" label="Date:" label-for="form-date-edit-input">
                <b-form-input id="form-date-edit-input"
                            type="date"  
                            v-model="editShowForm.date"
                            required
                            placeholder="Enter date">
                </b-form-input>
            </b-form-group>

            <b-form-group id="form-time-edit-group" label="Time:" label-for="form-time-edit-input">
                <b-form-input id="form-time-edit-input"
                            type="time" 
                            v-model="editShowForm.time"
                            required
                            placeholder="Enter time">
                </b-form-input>
            </b-form-group>

            <b-form-group id="form-venue-edit-group" label="Venue:" label-for="form-venue-edit-input">
                <b-form-select id="form-venue-edit-input" v-model="editShowForm.venue" required placeholder="Select venue">
                    <option v-for="venue in venues" :key="venue.id" :value="venue.id">
                        {{ venue.name }} {{ venue.place }} {{ venue.city }}
                    </option>
                </b-form-select>
            </b-form-group>


            <b-button type="submit" variant="outline-info">Submit</b-button>
            <b-button type="cancel" variant="outline-danger">cancel</b-button>

        </b-form>
        </b-modal>

    </div>
      </div>
      <br> <br>
      <button @click="logout" class="btn btn-danger">Logout</button>

    </div>
    
  </template>
  
  <script>
  import axios from 'axios';
  
  export default {
    data() {
      return {
        venues: [],
        shows: [],
        addVenueForm : {
                name : "",
                place : "",
                city : "",
                capacity : "",
                shows: [],
            },
            editForm : {
                
                name : "",
                place : "",
                city : "",
                capacity : "",
                shows: [],

            },
            
            availableShows: [],

            addShowForm : {
                show_name : "",
                genre : "",
                rating : "",
                price : "",
                date: "",
                time: "",
                available_seats: "",
                venue : "",
            },
            editShowForm : {
                show_name : "",
                genre : "",
                rating : "",
                price : "",
                date: "",
                time: "",
                available_seats: "",
                venue : "",
                
            },
            message : "",
            showMessage: false,
        // ... Other data properties ...
      };
    },
   
    methods: {
  // Venue Methods
  getVenues() {
    const path = 'http://localhost:5000/admindashboard/venues';
    axios.get(path)
      .then((res) => {
        this.venues = res.data.venues;
      })
      .catch((err) => {
        console.error(err);
      });
  },
  addVenue(payload) {
    const path = 'http://localhost:5000/admindashboard/venues';
    axios.post(path, payload)
      .then(() => {
        this.getVenues();
        this.showMessage = true;
        this.message = "Venue added!";
      })
      .catch((err) => {
        console.error(err);
        this.getVenues();
      });
  },
  updateVenue(payload, venueID) {
    const path = `http://localhost:5000/admindashboard/venues/${venueID}`;
    axios.put(path, payload)
      .then(() => {
        this.getVenues();
        this.showMessage = true;
        this.message = "Venue updated!";
      })
      .catch((err) => {
        console.error(err);
        this.getVenues();
      });
  },
  removeVenue(venueID) {
    const path = `http://localhost:5000/admindashboard/venues/${venueID}`;
    axios.delete(path)
      .then(() => {
        this.getVenues();
        this.showMessage = true;
        this.message = "Venue deleted!";
      })
      .catch((err) => {
        console.error(err);
        this.getVenues();
      });
  },
  deleteVenue(venue) {
    this.removeVenue(venue.id);
  },
  editVenue(venue) {
    this.editForm = { ...venue };
  },
  
  // Show Methods
  getShows() {
    const path = 'http://localhost:5000/admindashboard/shows';
    axios.get(path)
      .then((res) => {
        this.shows = res.data.shows;
      })
      .catch((err) => {
        console.error(err);
      });
  },
  addShow(payload) {
    const path = 'http://localhost:5000/admindashboard/shows';
    axios.post(path, payload)
      .then(() => {
        this.getShows();
        this.showMessage = true;
        this.message = "Show added!";
      })
      .catch((err) => {
        console.error(err);
        this.getShows();
      });
  },
  updateShow(payload, showID) {
    const path = `http://localhost:5000/admindashboard/shows/${showID}`;
    axios.put(path, payload)
      .then(() => {
        this.getShows();
        this.showMessage = true;
        this.message = "Show updated!";
      })
      .catch((err) => {
        console.error(err);
        this.getShows();
      });
  },
  removeShow(showID) {
    const path = `http://localhost:5000/admindashboard/shows/${showID}`;
    axios.delete(path)
      .then(() => {
        this.getShows();
        this.showMessage = true;
        this.message = "Show deleted!";
      })
      .catch((err) => {
        console.error(err);
        this.getShows();
      });
  },
  deleteShow(show) {
    this.removeShow(show.id);
  },
  editShow(show) {
    this.editShowForm = { ...show };
  },
  
  // Other Methods
  initForm() {
    // Initialize your form fields here
  },
  onSubmit(e) {
    e.preventDefault();
    this.$refs.addVenueModal.hide();
    this.$refs.addShowModal.hide();

    // Handle venue addition
    const venuePayload = {
      name: this.addVenueForm.name,
      place: this.addVenueForm.place,
      city: this.addVenueForm.city,
      capacity: this.addVenueForm.capacity,
      shows: this.addVenueForm.shows,
    };
    this.addVenue(venuePayload);

    // Handle show addition
    const showPayload = {
      show_name: this.addShowForm.name,
      genre: this.addShowForm.genre,
      rating: this.addShowForm.rating,
      price: this.addShowForm.price,
      date: this.addShowForm.date,
      time: this.addShowForm.time,
      available_seats: this.addShowForm.available_seats,
      venue_id: this.addShowForm.venue,
    };
    this.addShow(showPayload);

    this.initForm();
  },
  onReset(e) {
    e.preventDefault();
    this.$refs.addShowModal.hide();
    this.$refs.addVenueModal.hide();
    this.initForm();
  },
  onSubmitUpdate(e) {
    e.preventDefault();
    this.$refs.editVenueModal.hide();
    this.$refs.editShowModal.hide();
    
    const payload = {
      name: this.editForm.name,
      place: this.editForm.place,
      city: this.editForm.city,
      capacity: this.editForm.capacity,
      shows: this.editForm.shows,
      show_name: this.editShowForm.name,
      genre: this.editShowForm.genre,
      rating: this.editShowForm.rating,
      price: this.editShowForm.price,
      date: this.editShowForm.date,
      time: this.editShowForm.time,
      available_seats: this.editShowForm.available_seats,
      venue_id: this.editShowForm.venue,
    };
    this.updateShow(payload, this.editShowForm.id);
    this.updateVenue(payload, this.editForm.id);
  },
  onResetUpdate(e) {
    e.preventDefault();
    this.$refs.editShowModal.hide();
    this.$refs.editVenueModal.hide();
    this.initForm();
    this.getShows();
    this.getVenues();
  },
  logout() {
      // Perform any necessary logout actions, such as clearing session data, tokens, or local storage.
      // For simplicity, we will redirect to the admin login page.
      this.$router.push({ name: "AdminLogin" });
    },
  created() {
    this.getShows();
    this.getVenues();
  },

    
    }
}
</script>
