
<template>
    <div class="user-login">
        <div class="jumbotron vertical-cent">
        <div class="container">
          <!-- Add the Bootstrap stylesheet link here -->
          <link rel="stylesheet" href="https://cdn.jsdelivr.net/npm/bootswatch@4.5.2/dist/cosmo/bootstrap.min.css" integrity="sha384-5QFXyVb+lrCzdN228VS3HmzpiE7ZVwLQtkt+0d9W43LQMzz4HBnnqvVxKg6O+04d" crossorigin="anonymous">
  
          <div class="row">
            <div class="col-sm-12">
              <br /><br />
              <h1 class="text-center">User Login</h1>
      
      <form @submit.prevent="login">
        <div class="form-group">
          <label for="username">Username</label>
          <input type="text" id="username" v-model="username" class="form-control" required>
        </div>
        <div class="form-group">
          <label for="password">Password</label>
          <input type="password" id="password" v-model="password" class="form-control" required>
        </div>
        <button type="submit" class="btn btn-primary">Login</button>
      </form>
<br> <br>
      <!-- Add a button to navigate to the registration page -->
    <button @click="navigateToRegister">Register</button>
            </div>
          </div>
          </div>
          </div>
    </div>
  </template>
<script>
// import axios from 'axios';

export default {
  data() {
    return {
      username: '',
      password: '',
    };
  },
  methods: {
    login() {
        const userUsername = this.username;
        const userPassword = this.password;

        if (this.username === userUsername && this.password === userPassword) {
        // If the login is successful, redirect to the admin dashboard or display a success message.
        this.$router.push({ name: "UserDashboard" }); // Replace "AdminDashboard" with the actual route name of your admin dashboard.
      } else {
        this.error = "Invalid username or password.";
      }
    },
    navigateToRegister() {
      this.$router.push('/userregistration'); // Navigate to the registration page
    },
  },
};
</script>