<template>
  <div>
    <h2>Booking Form</h2>
    <form @submit.prevent="submitBookingForm">
      <!-- Form fields here -->
      <div class="form-group">
        <label for="show-name">Show Name:</label>
        <input v-model="showname" type="text" class="form-control" id="show-name" required>
      </div>
      <div class="form-group">
        <label for="venue-name">Venue Name:</label>
        <input v-model="venuename" type="text" class="form-control" id="venue-name" required>
      </div>
      <div class="form-group">
        <label for="show-time">Show Time:</label>
        <input v-model="showtime" type="time" class="form-control" id="show-time" required>
      </div>
      <div class="form-group">
        <label for="booking-date">Date:</label>
        <input v-model="date" type="date" class="form-control" id="booking-date" required>
      </div>
      <div class="form-group">
        <label for="num-seats">Number of Seats:</label>
        <select v-model.number="number_of_seats" class="form-control" id="num-seats" required>
          <option v-for="n in availableSeats" :key="n" :value="n">{{ n }}</option>
        </select>
      </div>
      <button type="submit" class="btn btn-primary">Book</button>

      
    </form>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      showname: "",
      venuename: "",
      showtime: "",
      date: "",
      number_of_seats: 1,
      availableSeats: [1, 2, 3, 4, 5],
    };
  },
  methods: {
    submitBookingForm() {
      const bookingData = {
        showname: this.showname,
        venuename: this.venuename,
        showtime: new Date(`2000-01-01T${this.showtime}:00`),
        date: this.date,
        number_of_seats: this.number_of_seats,
      };

      axios
        .post("http://localhost:5000/book_show", bookingData) // Adjust the URL as needed
        .then(response => {
          console.log("Booking successful:", response.data);
          // Optionally, you can perform other actions here, such as showing a success message.
        })
        .catch(error => {
          console.error("Error booking show:", error);
        });
    },
  
  
},
  
};
</script>