<template>
    <div class="user-dashboard">
      <div class="jumbotron vertical-cent">
        <div class="container">
          <!-- Add the Bootstrap stylesheet link here -->
          <link rel="stylesheet" href="https://cdn.jsdelivr.net/npm/bootswatch@4.5.2/dist/cosmo/bootstrap.min.css" integrity="sha384-5QFXyVb+lrCzdN228VS3HmzpiE7ZVwLQtkt+0d9W43LQMzz4HBnnqvVxKg6O+04d" crossorigin="anonymous">
  
          <div class="row">
            <div class="col-sm-12">
              <br /><br />
              <h2 class="text-center">Welcome to User Dashboard</h2>
  
              <!-- Search Bar -->
              <div class="input-group mb-3">
                <input
                  type="text"
                  class="form-control"
                  v-model="searchQuery"
                  @input="search"
                  placeholder="Search venues and shows"
                />
              </div>

              <!-- Venues Table -->
            <div class="table-responsive">
              <h3 class="text-center">Venues</h3>
              <table class="table table-bordered">
                <thead>
                  <tr>
                    <th>Name</th>
                    <th>City</th>
                    <th>Place</th>
                    <th>Capacity</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="venue in venues" :key="venue.id">
                    <td>{{ venue.name }}</td>
                    <td>{{ venue.city }}</td>
                    <td>{{ venue.place }}</td>
                    <td>{{ venue.capacity }}</td>
                  </tr>
                </tbody>
              </table>
            </div>

  
              <!-- Shows Table -->
              <div class="table-responsive">
                <h3 class="text-center">Shows</h3>
                <table class="table table-bordered">
                  <thead>
                    <tr>
                      <th>Name</th>
                      <th>Date</th>
                      <th>Time</th>
                      <th>Venue Name</th>
                      <th>Action</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="show in shows" :key="show.id">
                      <td>{{ show.show_name }}</td>
                      <td>{{ show.date }}</td>
                      <td>{{ show.time }}</td>
                      <td>{{ show.venue.name }}</td>
                      <td><button class="btn btn-primary" @click="bookShow(show)">Book</button></td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>

      
    <div>
      <h2>Booking Info</h2>
      <div v-if="booking">
        <p><strong>Show Name:</strong> {{ booking.showname }}</p>
        <p><strong>Venue Name:</strong> {{ booking.venuename }}</p>
        <p><strong>Show Time:</strong> {{ booking.showtime }}</p>
        <p><strong>Date:</strong> {{ booking.date }}</p>
        <p><strong>Number of Seats:</strong> {{ booking.number_of_seats }}</p>
      </div>
      <div v-else>
        <p>No booking information available.</p>
      </div>
    </div>
  
  

      <!-- Booking Modal -->
      <!-- <div class="modal" tabindex="-1" role="dialog" ref="bookingModal">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Book Show: {{ Show.show_name }}</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <p>Select the number of seats:</p>
            <input type="number" v-model="bookingForm.number_of_seats" min="1" :max="maxSeats" />
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">Cancel</button>
            <button type="button" class="btn btn-primary" @click="submitBookingForm">Book</button>
          </div>
        </div>
      </div>
    </div> -->
    <br> <br> 
    <!-- <button @click="navigateToBookShow">Book Show</button> -->

    <br> <br> 
    <button @click="logout" class="btn btn-danger">Logout</button>
    </div>
  </template>



<script>
import axios from "axios";

export default {
  data() {
    return {
      venues: [],
      shows: [],
      searchQuery: "",
      booking:[],
    
    };
  },
  created() {
    // Fetch user's bookings when the component is created
    this.fetchVenues();
    this.fetchShows();
    this.fetchBookings();
  },
  methods: {
    fetchVenues() {
      // Make a GET request to fetch venue details
      axios
        .get("http://localhost:5000/admindashboard/venues") // Adjust the URL accordingly
        .then((response) => {
          this.venues = response.data.venues;
        })
        .catch((error) => {
          console.error("Error fetching venues:", error);
        });
    },
    fetchShows() {
      // Make a GET request to fetch all shows
      axios
        .get("http://localhost:5000/admindashboard/shows") // Adjust the URL accordingly
        .then((response) => {
          this.shows = response.data.shows;
        })
        .catch((error) => {
          console.error("Error fetching shows:", error);
        });
    },

    fetchBookings() {
      // Make a GET request to fetch all shows
      axios
        .get("http://localhost:5000/book_show") // Adjust the URL accordingly
        .then((response) => {
          this.bookings = response.data.bookings;
        })
        .catch((error) => {
          console.error("Error fetching bookings:", error);
        });
    },
   
    search() {
      // Perform search for venues...
      axios.get(`http://localhost:5000/search/venues?query=${this.searchQuery}`)
        .then((response) => {
          this.venues = response.data.venues;
        })
        .catch((error) => {
          console.error('Error while searching venues:', error);
        });

      // Perform search for shows...
      axios.get(`http://localhost:5000/search/shows?query=${this.searchQuery}`)
        .then((response) => {
          this.shows = response.data.shows;
        })
        .catch((error) => {
          console.error('Error while searching shows:', error);
        });
    },
    bookShow() {
      // Navigate to the "Book Show" page with the selected show's ID
      this.$router.push({ name: 'BookShow' });
    },

    logout() {
      // Perform any necessary logout actions, such as clearing session data, tokens, or local storage.
      // For simplicity, we will redirect to the admin login page.
      this.$router.push({ name: "UserLogin" });
    },


 
  },
};
</script>